:host {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}

.novel-right-sidebar {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 350px;
  z-index: 1002;
  margin: 0 !important;
  padding: 0 !important;
  background: var(--bg-primary);
  border-left: 1px solid var(--border-color);
  box-shadow: -2px 0 8px var(--shadow);
  animation: slideInFromRight 0.3s ease-out;
  transition: width 0.3s ease;

  &.collapsed {
    width: 48px;
    overflow: hidden;
    
    .collapsed-view {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 12px 0;
      height: 100%;
      overflow-y: auto;
      cursor: pointer;
      
      .icon-list {
        display: flex;
        flex-direction: column;
        gap: 16px;
        width: 100%;
        
        .menu-icon {
          font-size: 24px;
          padding: 8px;
          text-align: center;
          transition: all 0.2s ease;
          border-radius: 8px;
          
          &:hover {
            background: var(--bg-secondary);
            transform: scale(1.1);
          }
        }
      }
    }
  }

  .main-menu,
  .expanded-panel {
    height: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
  }

  .sidebar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 20px;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-secondary);
    flex-shrink: 0;
    
    h3 {
      margin: 0;
      font-size: 16px;
      font-weight: 600;
      color: var(--text-primary);
    }

    .close-panel-btn {
      background: none;
      border: none;
      font-size: 24px;
      cursor: pointer;
      color: var(--text-muted);
      padding: 0;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease;

      &:hover {
        color: var(--accent-danger);
        transform: rotate(90deg);
      }
    }
  }

  .generation-status {
    padding: 12px 20px;
    background: linear-gradient(135deg, #ffeaa7, #fdcb6e);
    border-bottom: 1px solid var(--border-color);
    margin: 0;
    flex-shrink: 0;
    
    .status-text {
      font-size: 14px;
      color: #2d3436;
      font-weight: 500;
    }
    
    &.generating {
      animation: generatePulse 1.5s infinite;
    }
  }

  @keyframes generatePulse {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.7;
    }
  }

  .panel-content {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  .panel-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 20px;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-secondary);
    flex-shrink: 0;

    h3 {
      flex: 1;
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      color: var(--text-primary);
    }

    .back-btn,
    .close-panel-btn {
      background: none;
      border: none;
      font-size: 28px;
      cursor: pointer;
      color: var(--text-muted);
      padding: 0;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s ease;
    }

    .back-btn:hover {
      color: var(--accent-primary);
      transform: translateX(-2px);
    }

    .close-panel-btn:hover {
      color: var(--accent-danger);
      transform: rotate(90deg);
    }
  }

  .category-list {
    padding: 12px;
    flex: 1;
    overflow-y: auto;

    .category-item {
      display: flex;
      align-items: center;
      gap: 16px;
      padding: 16px;
      margin-bottom: 8px;
      background: var(--bg-secondary);
      border-radius: 12px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-tertiary);
        border-color: var(--primary-color);
        transform: translateX(-4px);
        box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
      }

      .category-icon {
        font-size: 32px;
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--bg-tertiary);
        border-radius: 10px;
      }

      .category-content {
        flex: 1;

        .category-title {
          font-size: 15px;
          font-weight: 600;
          color: var(--text-primary);
          margin-bottom: 4px;
        }

        .category-count {
          font-size: 12px;
          color: var(--text-secondary);
        }
      }

      .category-arrow {
        font-size: 24px;
        color: var(--text-secondary);
        opacity: 0.5;
        transition: all 0.2s ease;
      }

      &:hover .category-arrow {
        opacity: 1;
        transform: translateX(-4px);
      }
    }
  }

  .panel-items {
    padding: 12px;
    flex: 1;
    overflow-y: auto;

    .menu-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 14px 16px;
      margin-bottom: 8px;
      background: var(--bg-secondary);
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-tertiary);
        border-color: var(--primary-color);
        transform: translateX(-4px);
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
      }

      .item-content {
        flex: 1;
        display: flex;
        align-items: center;
        gap: 12px;

        .item-icon {
          font-size: 20px;
          width: 24px;
          text-align: center;
        }

        .item-text {
          flex: 1;

          .item-label {
            font-size: 14px;
            font-weight: 600;
            color: var(--text-primary);
            margin-bottom: 4px;
          }

          .item-description {
            font-size: 12px;
            color: var(--text-secondary);
            opacity: 0.8;
          }
        }
      }

      .item-arrow {
        font-size: 20px;
        color: var(--text-secondary);
        opacity: 0.5;
        transition: all 0.2s ease;
      }

      &:hover .item-arrow {
        opacity: 1;
        transform: translateX(-4px);
      }
    }
  }

  .novel-info-section {
    padding: 16px 20px;
    border-top: 1px solid var(--border-color);
    background: var(--bg-secondary);
    flex-shrink: 0;
    
    .info-title {
      font-size: 12px;
      font-weight: 600;
      color: var(--text-secondary);
      margin-bottom: 12px;
      text-transform: uppercase;
      letter-spacing: 0.5px;
    }
    
    .info-items {
      display: flex;
      flex-direction: column;
      gap: 8px;
      
      .info-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        .info-label {
          font-size: 14px;
          color: var(--text-secondary);
        }
        
        .info-value {
          font-size: 14px;
          font-weight: 500;
          color: var(--text-primary);
        }
      }
    }
  }

  @keyframes slideInFromRight {
    from {
      transform: translateX(100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
}

// 반응형 디자인
@media (max-width: 992px) {
  .novel-right-sidebar {
    width: 320px;
  }
}

@media (max-width: 768px) {
  .novel-right-sidebar {
    width: 100%;
    max-width: 350px;
  }
}
